import React from 'react';
import styles from './index.module.scss';
import { injectIntl, FormattedMessage, defineMessages } from 'react-intl';
import { withRouter } from 'react-router';

import { Link } from 'react-router-dom';
import isEqual from 'lodash/isEqual';
import memoizeOne from 'memoize-one';
import pathToRegexp from 'path-to-regexp';
import routes from '../../routes';
import { urlToList } from '../_utils/pathTools';

import { Layout, Menu, Icon, Button } from 'antd';
import { loadState } from '../../utils/localStorage';
const { SubMenu } = Menu;
const { Sider } = Layout;

// Allow menu.js config icon as string or ReactNode
//   icon: 'setting',
//   icon: 'http://demo.com/icon.png',
//   icon: <Icon type="setting" />,
const getIcon = icon => {
  if (typeof icon === 'string' && icon.indexOf('http') === 0) {
    return (
      <img
        src={icon}
        alt="icon"
        className={`${styles.icon} sider-menu-item-img`}
      />
    );
  }
  if (typeof icon === 'string') {
    return <Icon type={icon} />;
  }
  return icon;
};

const conversionPath = path => {
  if (path && path.indexOf('http') === 0) {
    return path;
  }
  return `/${path || ''}`.replace(/\/+/g, '/');
};

/**
 * 判断是否是http链接.返回 Link 或 a
 */
const getMenuItemPath = (location, item) => {
  const itemPath = '/app' + conversionPath(item.path);
  const icon = getIcon(item.icon);
  const { target, name, intlId } = item;
  // Is it a http link
  if (/^https?:\/\//.test(itemPath)) {
    return (
      <a href={itemPath} target={target}>
        {icon}
        <span><FormattedMessage id={intlId} defaultMessage={name} /></span>
      </a>
    );
  }

  return (
    <Link
      to={itemPath}
      target={target}
      replace={itemPath === location.pathname}
    >
      {icon}
      <span><FormattedMessage id={intlId} defaultMessage={name} /></span>
    </Link>
  );
};

/**
 * 获得菜单子节点
 * @memberof SiderMenu
 */
const getDefaultCollapsedSubMenus = props => {
  const {
    location: { pathname }
  } = props;
  const menuPath = pathname.replace(/^\/app/, '');
  return urlToList(menuPath)
    .map(item => getMenuMatches(flatMenuKeys, item)[0])
    .filter(item => item)
    .map(item => '/app' + item);
};

/**
 * Recursively flatten the data
 * [{path:string},{path:string}] => {path,path2}
 * @param  menu
 */
const getFlatMenuKeys = menu =>
  menu.reduce((keys, item) => {
    keys.push(item.path);
    if (item.children) {
      return keys.concat(getFlatMenuKeys(item.children));
    }
    return keys;
  }, []);

const getMenuMatches = memoizeOne(
  (flatMenuKeys, path) =>
    flatMenuKeys.filter(item => item && pathToRegexp(item).test(path)),
  isEqual
);

const flatMenuKeys = getFlatMenuKeys(routes);

class SiderNav extends React.PureComponent {
  constructor(props) {
    super(props);
    this.getSelectedMenuKeys = memoizeOne(this.getSelectedMenuKeys, isEqual);
    this.state = {
      collapsed: false,
      userInfo: loadState('userInfo', true) || {},
      openKeys: getDefaultCollapsedSubMenus(props)
    };
  }

  /**
   * get SubMenu or Item
   */
  getSubMenuOrItem = (location, item) => {
    const {icon, name, intlId, path} = item
    if (item.children && item.children.some(child => child.name)) {
      const childrenItems = this.getNavMenuItems(location, item.children);
      // 当无子菜单时就不展示菜单
      if (childrenItems && childrenItems.length > 0) {
        return (
          <SubMenu
            title={
              icon ? (
                <span>
                  {getIcon(icon)}
                  <span><FormattedMessage id={intlId} defaultMessage={name} /></span>
                </span>
              ) : (
                <FormattedMessage id={intlId} defaultMessage={name} />
              )
            }
            key={'/app' + path}
          >
            {childrenItems}
          </SubMenu>
        );
      }
      return null;
    } else {
      return (
        <Menu.Item key={'/app' + path}>
          {getMenuItemPath(location, item)}
        </Menu.Item>
      );
    }
  };

  /**
   * 获得菜单子节点
   */
  getNavMenuItems = (location, menusData) => {
    if (!menusData) {
      return [];
    }
    // 路由配置中有hideInMenu,菜单不显示
    // 路由配置中存在item.role,但和当前登录用于的权限不匹配，菜单不显示
    return menusData
      .filter(item => {
        const matchRole =
          !item.role || item.role.indexOf(this.state.userInfo.role) !== -1;
        return item.name && !item.hideInMenu && matchRole;
      })
      .map(item => {
        // make dom
        const ItemDom = this.getSubMenuOrItem(location, item);
        return ItemDom;
      });
  };

  componentDidMount() {
    console.log('sider nav did mount');
  }

  static getDerivedStateFromProps(props, state) {
    const { pathname } = state;
    if (props.location.pathname !== pathname) {
      return {
        pathname: props.location.pathname,
        openKeys: getDefaultCollapsedSubMenus(props)
      };
    }
    return null;
  }

  isMainMenu = key => {
    const menuData = routes;
    return menuData.some(item => {
      if (key) {
        return item.key === key || item.path === key;
      }
      return false;
    });
  };

  handleOpenChange = openKeys => {
    const moreThanOne =
      openKeys.filter(openKey => this.isMainMenu(openKey)).length > 1;
    this.setState({
      openKeys: moreThanOne ? [openKeys.pop()] : [...openKeys]
    });
  };

  onCollapse = (collapsed, type) => {
    console.log('onCollapse:', collapsed, type);
    if (!collapsed) {
      if (this.state.openKeys.length === 0) {
        let openKeys = getDefaultCollapsedSubMenus(this.props);
        this.setState({ openKeys });
      }
    }
    this.setState({ collapsed });
  };

  // Get the currently selected menu
  getSelectedMenuKeys = pathname => {
    pathname = pathname.replace(/^\/app/, '');
    return urlToList(pathname)
      .map(itemPath => {
        // pop()会改变原数组
        let list = getMenuMatches(flatMenuKeys, itemPath)
        return list[list.length - 1]
      }).map(item => '/app' + item);
  };

  render() {
    const { location } = this.props;
    const { pathname } = location;
    // let selectedKeys = [pathname];

    const { collapsed, openKeys } = this.state;

    // if pathname can't match, use the nearest parent's key
    let selectedKeys = this.getSelectedMenuKeys(pathname);
    if (!selectedKeys.length && openKeys) {
      selectedKeys = [openKeys[openKeys.length - 1]];
    }

    let props = {};
    if (!collapsed) {
      if (openKeys) {
        props = {
          openKeys
        };
      }
    }

    return (
      <Sider
        breakpoint="lg"
        onBreakpoint={broken => {
          console.log(broken);
        }}
        collapsible
        trigger={
          <Icon type={this.state.collapsed ? 'menu-unfold' : 'menu-fold'} />
        }
        collapsed={this.state.collapsed}
        onCollapse={this.onCollapse}
        className={styles.sider}
      >
        <Menu
          theme={'dark'}
          mode="inline"
          selectedKeys={selectedKeys}
          onOpenChange={this.handleOpenChange}
          {...props}
        >
          {this.getNavMenuItems(location, routes)}
        </Menu>
      </Sider>
    );
  }
}

export default injectIntl(withRouter(SiderNav));
