import React from 'react';

const style = {
  width: '100%',
  height: '100%',
  position: 'absolute',
  left: 0,
  top: 0,
  zIndex: -1,
  visibility: 'inherit'
};

export default ({ zIndex = -1 }) => {

  let styleObj = {...style, zIndex}

  return (
    <iframe
      frameBorder="0"
      marginWidth="0"
      marginHeight="0"
      scrolling="no"
      style={styleObj}
    />
  );
};
