import React from 'react';
import { injectIntl, FormattedMessage, defineMessages } from 'react-intl';
import { Icon, Button, Menu, Dropdown, Divider } from 'antd';

import LoginForm from '../../components/LoginForm';

import {saveState, loadState} from '../../utils/localStorage'

import styles from './index.module.css';
// import logo from './logo.png';
import logoEn from './logo-title-en.png';
import logoZh from './logo-title-zh.png';

const messages = defineMessages({
  appName: {
    id: 'common.appName',
    defaultMessage: '优云视',
  },
  lang: {
    id: 'header.lang',
    defaultMessage: '语言'
  },
  zhCN: {
    id: 'header.zhCN',
    defaultMessage: '中文(简体)'
  },
  zhTW: {
    id: 'header.zhTW',
    defaultMessage: '中文(繁体)'
  },
  enUS: {
    id: 'header.enUS',
    defaultMessage: '英文'
  },
  hello: {
    id: 'header.hello',
    defaultMessage: '您好'
  }

});
class Login extends React.Component {

  handleMenuClick = ({key}) => {
    saveState('lang', key, true)
    window.location.reload()
  }

  render() {
    return (
      <div className={styles.login_container}>
        <div className={styles.logo}>
          <img src={injectIntl.locale === 'en' ? logoEn : logoZh} alt="logo" />
          {/* <h1><FormattedMessage {...messages.appName} /></h1> */}
        </div>

        <LoginForm />

        <div className={styles.copyright}>
          &copy; 2019 ULSee,V1.0.2&nbsp;
          {/* 语言切换 */}
          {/* <Divider type="vertical"/>
          <Dropdown overlay={<Menu onClick={this.handleMenuClick}>
            <Menu.Item key="zh_CN"><FormattedMessage {...messages.zhCN} /></Menu.Item>
            <Menu.Item key="zh_TW"><FormattedMessage {...messages.zhTW} /></Menu.Item>
            <Menu.Item key="en_US"><FormattedMessage {...messages.enUS} /></Menu.Item>
          </Menu>}>
          <Icon type="global" title={this.props.intl.formatMessage(messages.lang)} style={{fontSize: 14}}/>
        </Dropdown>  */}

        </div>
      </div>
    );
  }
}

export default injectIntl(Login)

