import React from 'react';
import { loadState, saveState, removeState } from '../utils/localStorage';
import { AuthUserContext } from './AuthUserContext';
import { login as doLogin, logout as doLogout, getAccountInfo} from '../api/account'
import { message,Spin } from 'antd';
import { injectIntl, FormattedMessage, defineMessages } from 'react-intl';
import { reject } from 'rsvp';

const AUTH_USER_KEY = "authUser"
const AUTH_USER_INFO = 'userInfo'

const messages = defineMessages({
  userOrPwdErr: {
    id: 'login.userOrPwdErr',
    defaultMessage: '用户名或密码错误!'
  }
})

const withAuthentication = Component => {
  class WithAuthentication extends React.Component {
    constructor(props) {
      super(props);
      const authUser = loadState(AUTH_USER_KEY, true)
      this.state = {
        authUser: authUser,
        login: this.login,
        logout: this.logout,
        loading: false,
      };
      // 获取账户信息
      this.getAccount().then((res) => {
        const {account_id, account_name, role, group_id} = res.data
        saveState(AUTH_USER_KEY, account_name, true)
        saveState(AUTH_USER_INFO, {
          account_id,
          account_name,
          group_id,
          role: role.role_id,
        }, true)
      }).catch((e) => {
        console.log(e)
      });
    }

    login = (user, history) => {
      this.setState({ loading: true });
      doLogin(user.username, user.password)
      .then(response => {
        if (response.code === 1000) {

          this.setState({ authUser: user.username });
          saveState(AUTH_USER_KEY, user.username, true)
          // 获取账户信息
          this.getAccount().then((res) => {
            const {account_id, account_name, role } = res.data
            saveState(AUTH_USER_INFO, {
              account_id,
              account_name,
              role: role.role_id,
            }, true)
          }).catch((e) => {
            console.log(e)
          }).then(() => {
            if (history) {
              history.push('/');
            }
          })
        } else {
          message.error(response.error || this.props.intl.formatMessage(messages.userOrPwdErr))
        }
      })
      .catch(error => {
        // handle error
        console.log('oops:',error);
        message.error(this.props.intl.formatMessage(messages.userOrPwdErr));
      })
      .then(() => {
        // always executed
        this.setState({ loading: false });
      });

    };

    getAccount = () => {
      var p = new Promise((resolve, rejected) => {
        getAccountInfo().then(res => {
          if (res.code === 1000) {
            resolve(res)
          } else {
            rejected(res.msg);
          }
        }).catch(e => {
          rejected(e);
        });
      });
      return p;
    };

    logout = history => {
      this.setState({ loading: true });
      doLogout()
      .then(response => {
        if (response.code === 1000) {
          this.setState({ authUser: null, userInfo: null });
          removeState(AUTH_USER_KEY, true)
          removeState(AUTH_USER_INFO, true)
          // go to login page
          // already redirect to login page in AuthorizedRoute
          // history.push('/auth/login');
        }
      })
      .catch(error => {
        // handle error
        console.log('oops:',error);
        // message.warning('登出失败！');

        this.setState({ authUser: null, userInfo: null });
        removeState(AUTH_USER_KEY, true)
        removeState(AUTH_USER_INFO, true)
        // go to login page
        // already redirect to login page in AuthorizedRoute
        // history.push('/auth/login');
      })
      .then(() => {
        // always executed
        this.setState({ loading: false });
      });


    };
  
    render() {
      return (
      <AuthUserContext.Provider value={this.state}>
        <Spin spinning={this.state.loading}>
          <Component />
        </Spin>
      </AuthUserContext.Provider>
      );
    }
  };

  return injectIntl(WithAuthentication)

}

export default withAuthentication;
