import React from 'react';
import { injectIntl, FormattedMessage, defineMessages } from 'react-intl';
import styles from './index.module.css';
import { Form, Icon, Input, Button } from 'antd';
import { withRouter } from 'react-router';
import { AuthUserContext } from '../../session/AuthUserContext';

const FormItem = Form.Item;

const messages = defineMessages({
  login: {
    id: 'login.login',
    defaultMessage: '登录',
  },
  accountPlaceholder: {
    id: 'login.account.placeholder',
    defaultMessage: '请输入账号',
  },
  passwordPlaceholder: {
    id: 'login.password.placeholder',
    defaultMessage: '请输入密码'
  },
  accountRequired: {
    id: 'login.account.required',
    defaultMessage: '请输入你的账号!',
  },
  passwordRequired: {
    id: 'login.password.required',
    defaultMessage: '请输入你的密码!'
  },
});

class LoginForm extends React.Component {
  handleSubmit = e => {
    e.preventDefault();
    const { form, history, login } = this.props;
    form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
        // login().then(() => {
        //   history.push('/app');
        // });
        login(values, history);
      }
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form onSubmit={this.handleSubmit} className={styles.login_form}>
        <FormItem>
          {getFieldDecorator('username', {
            rules: [{ required: true, message: <FormattedMessage {...messages.accountRequired} /> }]
          })(
            <Input
              prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
              maxLength={30}
              placeholder={this.props.intl.formatMessage(messages.accountPlaceholder)}
              autoFocus={true}
              autoComplete="off"
            />
          )}
        </FormItem>
        <FormItem>
          {getFieldDecorator('password', {
            rules: [{ required: true, message: <FormattedMessage {...messages.passwordRequired} /> }]
          })(
            <Input
              prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
              type="password"
              maxLength={20}
              placeholder={this.props.intl.formatMessage(messages.passwordPlaceholder)}
            />
          )}
        </FormItem>
        <FormItem>
          <Button
            type="primary"
            htmlType="submit"
            className={styles["login-form-button"]}
          >
            <FormattedMessage {...messages.login} />
          </Button>
        </FormItem>
      </Form>
    );
  }
}

const WrapLoginForm = props => (
  <AuthUserContext.Consumer>
    {({ login }) => <LoginForm {...props} login={login} />}
  </AuthUserContext.Consumer>
);

export default injectIntl(withRouter(Form.create()(WrapLoginForm)));
