import axios from 'axios';
import { getApiServerAddress } from './address.js'
import createHistory from "history/createHashHistory"
import { removeState } from './localStorage';

const AUTH_USER_KEY = "authUser"

const history = createHistory()

let baseUrl = getApiServerAddress()
// Global axios defaults
axios.defaults.baseURL = baseUrl;
axios.defaults.withCredentials = true;
// axios.defaults.headers.common['Authorization'] = AUTH_TOKEN;
// axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';

// Interceptors
// You can intercept requests or responses before they are handled by then or catch.

let apiCancelTokens = [];

// Add a request interceptor
axios.interceptors.request.use(
  config => {
    // 添加取消请求用的cancelToken
    config.cancelToken = new axios.CancelToken(function executor(c) {
      apiCancelTokens.push(c);
    });

    // get请求加时间戳
    if (config.method === 'get') {
      config.params = {...config.params, '_': Date.now()}
    }

    return config;
  },
  error => {
    // Do something with request error
    return Promise.reject(error);
  }
);
// Add a response interceptor
axios.interceptors.response.use(
  function(response) {
    const data = response.data
    return data;
  },
  function(error) {
    const data = error.response
    if (data && data.status === 401) {
      removeState(AUTH_USER_KEY, true)
      // go to login page
      console.log('401')
      history.push('/auth/login');
      return;
    }
    // Do something with response error
    return Promise.reject(error);
  }
);

// 当路由跳转之前，取消当前页面的所有请求
export function cancelApiRequsets() {
  while (apiCancelTokens.length) {
    var cancel = apiCancelTokens.pop();
    cancel('请求被取消');
  }
}

export default axios;
