import React from 'react';
import { injectIntl, FormattedMessage, defineMessages } from 'react-intl';
import { Switch, Route, Redirect } from 'react-router-dom';
import styles from './PrimaryLayout.module.css';
import logo from './logo.png';
import HeaderRight from '../components/HeaderRight';
import SiderNav from '../components/SiderNav';
import routes from '../routes';


import { Layout } from 'antd';

const { Header, Content, Footer } = Layout;

const messages = defineMessages({
  appName: {
    id: 'common.appName',
    defaultMessage: '优云视',
  },
});

const getRoutes = routeData => {
  if (!routeData) {
    return [];
  }
  return routeData.filter(
    item => item.path && item.path.charAt(0) === '/' && item.private
  );
};

let flattedRoutes = [];
const getFlattedRoutes = routes => {
  flattedRoutes = [...flattedRoutes, ...getRoutes(routes)];
  routes.forEach(route => {
    if (route.children && route.children.length > 0) {
      getFlattedRoutes(route.children);
    }
  });
};

getFlattedRoutes(routes);

flattedRoutes = flattedRoutes.filter(item => item.component)


// 添加meta属性到路由中
const MetaRoute = ({ component: Component, ...rest }) => {
  return (
  <Route {...rest} render={props => (
      <Component {...props} meta={rest.meta}/>
  )}/>
)}



export default injectIntl(({ match }) => (
  <Layout className="page-primary-layout">
    <Header className={styles.header}>
      <div className={styles.logo}>
        <a href="/">
          <img src={logo} alt="logo" />
          <h1><FormattedMessage {...messages.appName} /></h1>
        </a>
      </div>
      <HeaderRight />
    </Header>
    <Layout>
      <SiderNav />
      <Layout style={{ padding: '24px 24px 0' }}>
        <Content
          className={styles.body_content}
        >
          <Switch>
            {flattedRoutes.map(route => (<MetaRoute
                key={route.path}
                exact
                path={`${match.path}${route.path}`}
                component={route.component}
                meta={route.meta}
              />
            )
            )}
            <Redirect
              exact
              from={match.path}
              to={`${match.path}/personnel-manage`}
            />
            <Redirect to={`${match.url}`} />
          </Switch>
        </Content>
        <Footer style={{ textAlign: 'center' }}>&copy; 2019 ULSee,V1.0.2</Footer>
      </Layout>
    </Layout>
  </Layout>
));
