import React from 'react';
import styles from './index.module.scss';
import { Divider, Icon, Menu, Dropdown, Button } from 'antd';
import { injectIntl, FormattedMessage, defineMessages } from 'react-intl';
import SignOutButton from '../SignOut';
import { Link } from 'react-router-dom';
import UpdatePassword from './UpdatePassword';
import { AuthUserContext } from '../../session/AuthUserContext';
import {saveState, loadState} from '../../utils/localStorage'

const menu = (
  <Menu>
    <Menu.Item>
      <UpdatePassword clsName={styles.update_text} />
    </Menu.Item>
  </Menu>
);

const messages = {
  lang: {
    id: 'header.lang',
    defaultMessage: '语言'
  },
  zhCN: {
    id: 'header.zhCN',
    defaultMessage: '中文(简体)'
  },
  zhTW: {
    id: 'header.zhTW',
    defaultMessage: '中文(繁体)'
  },
  enUS: {
    id: 'header.enUS',
    defaultMessage: '英文'
  },
  hello: {
    id: 'header.hello',
    defaultMessage: '您好'
  }
}


function handleMenuClick({key}) {
  saveState('lang', key, true)
  window.location.reload()
}

const UserName = ({ authUser }) => (
  <span className={`${styles.action} ${styles.welcome}`}>
    <FormattedMessage {...messages.hello} />，
    {authUser}
  </span>
)


export default injectIntl((props) => (
  <div className={styles.right}>
    <Dropdown overlay={menu}>
      <span>
        <AuthUserContext.Consumer>
          {({ authUser }) => <UserName authUser={authUser} />}
        </AuthUserContext.Consumer>
      </span>
    </Dropdown>
    {/* 语言切换 */}
    {/* <Divider type="vertical" className={styles.split}/>
      <Dropdown overlay={<Menu onClick={handleMenuClick}>
          <Menu.Item key="zh_CN"><FormattedMessage {...messages.zhCN} /></Menu.Item>
          <Menu.Item key="zh_TW"><FormattedMessage {...messages.zhTW} /></Menu.Item>
          <Menu.Item key="en_US"><FormattedMessage {...messages.enUS} /></Menu.Item>
        </Menu>}>
        <Icon type="global" title={props.intl.formatMessage(messages.lang)}/>
      </Dropdown>          */}
    <Divider type="vertical" className={styles.split} />
    <SignOutButton clsName={styles.action} />
  </div>
));
