const ENV_CONFIG = window.__env.config;

export const UPLOAD_SERVER = ENV_CONFIG.BatchUploadServer

export function getApiServerAddress() {
  let config = ENV_CONFIG
  let SERVER_IP = config.WEPAPI.SERVER_IP

  let url = SERVER_IP + config.WEPAPI.PATH
  return url
}

export function getWebsocketServerAddress() {
  let config = ENV_CONFIG
  let SERVER_IP = config.WEBSOCKET.SERVER_IP || window.location.host
  let PROTOCOL = config.WEBSOCKET.PROTOCOL
  PROTOCOL = PROTOCOL ? PROTOCOL : "ws";
  let url = PROTOCOL + "://" + SERVER_IP + config.WEBSOCKET.PATH

  return url
}

export function getStorageServerAddress() {
  let STORAGE = ENV_CONFIG.STORAGE
  let photoBaseUrl = STORAGE.SERVER_IP + STORAGE.PATH
  return photoBaseUrl
}

export function getH5Address() {
  const config = ENV_CONFIG;
  const { protocol, hostname } = window.location;
  const origin = `${protocol}//${hostname}`
  const SERVER_IP = config.WEBSOCKET.SERVER_IP || origin
  let url = `${SERVER_IP}:${config.H5.PORT}`;
  return url 
}

