import React from 'react';
import { Modal, Form, Input, Icon, message } from 'antd';
import { injectIntl, FormattedMessage, defineMessages } from 'react-intl';
import BgIframe from '../BgIframe'
import {modifyPassword} from '../../api/account'
import createHistory from "history/createHashHistory"
import { removeState } from '../../utils/localStorage';

const FormItem = Form.Item;
const history = createHistory()
const AUTH_USER_KEY = 'authUser'

const messages = {
  modifyPassword: {
      id: 'updatePassword.modifyPassword',
      defaultMessage: '修改密码',
  },
  oldPassword: {
      id: 'updatePassword.oldPassword',
      defaultMessage: '原密码',
  },
  newPassword: {
      id: 'updatePassword.newPassword',
      defaultMessage: '新密码',
  },
  confirmPassword: {
      id: 'updatePassword.confirmPassword',
      defaultMessage: '确认密码',
  },
  oldPasswordRequired: {
      id: 'updatePassword.oldPassword.required',
      defaultMessage: '请输入原密码!',
  },
  newPasswordRequired: {
    id: 'updatePassword.newPassword.required',
    defaultMessage: '请输入新密码!',
  },
  confirmPasswordRequired: {
    id: 'updatePassword.confirmPassword.required',
    defaultMessage: '请确认新密码!',
  },
  passwordNotIdentical: {
      id: 'updatePassword.error.passwordNotIdentical',
      defaultMessage: '新密码不一致!',
  },
  passwordChangeSuccessTip: {
      id: 'updatePassword.tip.success',
      defaultMessage: '密码修改成功，请重新登录',
  },
  passwordChangeFailTip: {
      id: 'updatePassword.tip.fail',
      defaultMessage: '密码修改失败!',
  },
  prompt: {
      id: 'common.prompt',
      defaultMessage: '提示'
  }
}

const CreateForm = Form.create()(
    class extends React.Component {
        state = {
            confirmDirty: false,
        };

        handleSubmit = (e) => {
            e.preventDefault();
            this.props.form.validateFieldsAndScroll((err, values) => {
                if (!err) {
                    console.log('Received values of form');
                }
            });
        }

        handleConfirmBlur = (e) => {
            const value = e.target.value;
            this.setState({ confirmDirty: this.state.confirmDirty || !!value });
        }

        compareToFirstPassword = (rule, value, callback) => {
            const form = this.props.form;
            if (value && value !== form.getFieldValue('new_passwd_a')) {
                callback(<FormattedMessage {...messages.passwordNotIdentical} />);
            } else {
                callback();
            }
        }

        validateToNextPassword = (rule, value, callback) => {
            const form = this.props.form;
            if (value && this.state.confirmDirty) {
                form.validateFields(['new_passwd_b'], { force: true });
            }
            callback();
        }

        render() {
            const { visible, onCancel, onCreate, form, locale } = this.props;
            const { getFieldDecorator } = form;

            const formItemLayout = {
                labelCol: {
                    xs: { span: 24 },
                    sm: { span: locale === 'en' ? 9 : 6 },
                },
                wrapperCol: {
                    xs: { span: 24 },
                    sm: { span: 14 },
                },
            };

            return (
                <Modal
                    visible={visible}
                    title={<FormattedMessage {...messages.modifyPassword} />}
                    onCancel={onCancel}
                    onOk={onCreate}
                    width={430}
                    getContainer={() => document.getElementById('video_container') || document.body}
                >
                    <Form onSubmit={this.handleSubmit}>
                        <FormItem
                            {...formItemLayout}
                            label={<FormattedMessage {...messages.oldPassword} />}
                        >
                            {getFieldDecorator('old_passwd', {
                                rules: [{
                                    required: true, message: <FormattedMessage {...messages.oldPasswordRequired} />,
                                }],
                            })(
                                <Input type="password" maxLength={20}/>
                            )}
                        </FormItem>
                        <FormItem
                            {...formItemLayout}
                            label={<FormattedMessage {...messages.newPassword} />}
                        >
                            {getFieldDecorator('new_passwd_a', {
                                rules: [{
                                    required: true, message: <FormattedMessage {...messages.newPasswordRequired} />,
                                }, {
                                    validator: this.validateToNextPassword,
                                }],
                            })(
                                <Input type="password" maxLength={20}/>
                            )}
                        </FormItem>
                        <FormItem
                            {...formItemLayout}
                            label={<FormattedMessage {...messages.confirmPassword} />}
                        >
                            {getFieldDecorator('new_passwd_b', {
                                rules: [{
                                    required: true, message: <FormattedMessage {...messages.confirmPasswordRequired} />,
                                }, {
                                    validator: this.compareToFirstPassword,
                                }],
                            })(
                                <Input type="password" onBlur={this.handleConfirmBlur} maxLength={20}/>
                            )}
                        </FormItem>

                    </Form>
                    <BgIframe />
                </Modal>
            );
        }
    }
);

class UpdatePassword extends React.Component {
    state = {
        visible: false,
    };

    showModal = () => {
        this.setState({ visible: true });
    }

    handleCancel = () => {
        this.setState({ visible: false });
    }

    handleCreate = () => {
        const form = this.formRef.props.form;
        form.validateFields((err, values) => {
            if (err) {
                return;
            }
            modifyPassword(values).then((res) =>{
                if (res.code === 1000) {
                  form.resetFields();
                  this.setState({ visible: false });
                  Modal.success({
                    title: this.props.intl.formatMessage(messages.prompt),
                    content: this.props.intl.formatMessage(messages.passwordChangeSuccessTip),
                    onOk: () => {
                      removeState(AUTH_USER_KEY, true);
                      history.push('/auth/login');
                    }
                  })
                } else {
                    message.error(res.msg || this.props.intl.formatMessage(messages.passwordChangeFailTip))
                }
            }).catch((e) => {
              message.error( this.props.intl.formatMessage(messages.passwordChangeFailTip))
              console.log(e)
            })
        });
    }

    saveFormRef = (formRef) => {
        this.formRef = formRef;
    }

    render() {
        return (
            <React.Fragment>
                <span onClick={this.showModal}>
                    <Icon type="key" theme="outlined" /><span className={this.props.clsName}><FormattedMessage {...messages.modifyPassword} /></span>
                </span>

                <CreateForm
                    wrappedComponentRef={this.saveFormRef}
                    visible={this.state.visible}
                    onCancel={this.handleCancel}
                    onCreate={this.handleCreate}
                    locale={this.props.intl.locale}
                />
            </React.Fragment>
        );
    }
}

export default injectIntl(UpdatePassword);