import React from 'react';

import Loadable from 'react-loadable';
import Loading from '../components/Loading';

const PersonnelManage = Loadable({
  loader: () => import('../pages/CommonPlatform/PersonnelManage'),
  loading: Loading
});

const AccountManage = Loadable({
  loader: () => import('../pages/CommonPlatform/AccountManage'),
  loading: Loading
});


const EquipmentManage = Loadable({
  loader: () => import('../pages/CommonPlatform/EquipmentManage'),
  loading: Loading
});


const Record = Loadable({
  loader: () => import('../pages/CommonPlatform/Record'),
  loading: Loading
});

const VisitorManage = Loadable({
  loader: () => import('../pages/CommonPlatform/VisitorManage'),
  loading: Loading
});

const SystemManage = Loadable({
  loader: () => import('../pages/CommonPlatform/SystemManage'),
  loading: Loading
})


const routes = [
  {
    private: true,
    name: '人员管理',
    intlId: 'menu.personnelManage',
    icon: 'team',
    path: '/personnel-manage',
    component: PersonnelManage,
  },
  {
    private: true,
    name: '设备管理',
    intlId: 'menu.equipmentManage',
    icon: 'video-camera',
    path: '/device-manage',
    component: EquipmentManage,
  },
  {
    private: true,
    name: '访客管理',
    intlId: 'menu.visitorManage',
    icon: 'user',
    path: '/visitor-manage',
    component: VisitorManage,
  },
  {
    private: true,
    name: '识别记录',
    intlId: 'menu.recognizeRecord',
    icon: 'file-done',
    path: '/recognize-record',
    component: Record,
  },
  // 主账号登录
  {
    private: true,
    name: '账号管理',
    icon: 'idcard',
    intlId: 'menu.accountManage',
    path: '/account-manage',
    component: AccountManage,
    role: [2],
    meta: {
      accountType: 2,
      role: 2,
    }
  },
  // admin登录
  {
    private: true,
    name: '账号管理',
    icon: 'idcard',
    intlId: 'menu.accountManage',
    path: '/main-account-manage',
    component: AccountManage,
    role: [1],
    meta: {
      // admin登录，进入主账号页面
      accountType: 1,
      role: 1,
    }
  },
  {
    private: true,
    name: '账号管理',
    icon: 'idcard',
    intlId: 'menu.accountManage',
    path: '/main-account-manage/account-manage/:id',
    component: AccountManage,
    role: [1],
    hideInMenu: true,
    meta: {
      // adamin登录，进入子账号页面
      accountType: 2,
      role: 1,
    }
  },
  // {
  //   private: true,
  //   name: '系统维护',
  //   intlId: 'menu.systemManage',
  //   icon: 'setting',
  //   path: '/system-manage',
  //   component: SystemManage,
  //   role: [1, 2],
  // },
];

export default routes;
