import "@babel/polyfill";
import React from 'react';
import ReactDOM from 'react-dom';
import './index.scss';
import App from './App';
import * as serviceWorker from './serviceWorker';
import { LocaleProvider } from 'antd';
import { addLocaleData, IntlProvider } from 'react-intl';

import moment from 'moment';
import 'moment/locale/zh-cn';
import 'moment/locale/zh-tw';

import appLocal from './utils/locale'
// import {crossTabStorrage} from './utils/localStorage'


import './resize'

// 解决promise 在ie中未定义的问题
if (!window.Promise) {
  window.Promise = Promise;
 }

moment.locale('en');
addLocaleData(appLocal.data);

// crossTabStorrage()

ReactDOM.render(
  
  <LocaleProvider locale={appLocal.antd}>
    <IntlProvider locale={appLocal.locale} messages={appLocal.messages}>
      <App />
    </IntlProvider>
  </LocaleProvider>,
  document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: http://bit.ly/CRA-PWA
serviceWorker.unregister();
