import fetch from '../utils/fetch'

const apis = {
  login: '/api/account/login',
  logout: '/api/account/logout',
  pwd: '/api/account/password',
  account: '/api/account',
  reset: '/api/account/reset',
  list: '/api/account/childs',
  addAccount: '/api/account/register',
  addSubAccount: '/api/account/register/child',
}

/**
 *登录
 *
 * @export
 * @param {String} username
 * @param {String} password
 * @returns
 */
export function login(username, password) {
  const data = {
    account_name: username,
    passwd: password
  }
  return fetch.post(apis.login, data);
}

/**
 *登出
 *
 * @export
 * @returns
 */
export function logout() {
  return fetch({
    url: apis.logout,
    method: 'post'
  });
}

/**
 *修改密码
 *
 * @export
 * @param {Object} data
 * @returns
 */
export function modifyPassword(data) {
  return fetch.post(apis.pwd, data)
}

/**
 *获取账户信息,id不传表示获取当前账户信息，否则表示获取子账户信息
 *
 * @export
 * @param {Number} id
 * @returns
 */
export function getAccountInfo(id) {
  return fetch.get(apis.account, {
    params: {
      account_id: id,
    }
  });
}

/**
 *更新子账户
 *
 * @export
 * @param {Object} data
 * @returns
 */
export function editAccount(data) {
  return fetch.put(apis.account, data);
}

/**
 *删除子账户
 *
 * @export
 * @param {Number} id
 * @returns
 */
export function deleteAccount(id) {
  return fetch.delete(apis.account, {
    data: {
      account_id: id
    }
  });
}

/**
 *添加账号
 *
 * @export
 * @param {Object} data
 * @returns
 */
export function addAccount(data) {
  return fetch.post(apis.addAccount, data);
}

// 添加子账号
export function addSubAccount(data) {
  return fetch.post(apis.addSubAccount, data);
}

/**
 *获取子账号列表
 * @param {Object} data
 * @export
 * @returns
 */
export function getAccounts(data) {
  return fetch.get(apis.list, {
    params: data
  });
}

/**
 *重置密码
 *
 * @export
 * @param {Number} id
 * @returns
 */
export function resetPwd(id) {
  return fetch.post(apis.reset,  {account_id: id});
}